// pages/cadastro-cliente.js
import React from 'react';
import { Button, TextField, Container, Typography } from '@mui/material';
import styled from 'styled-components';

const StyledContainer = styled(Container)`
  max-width: 600px;
  margin-top: 50px;
  padding: 20px;
  background-color: #f1efde; /* Cor de fundo */
  border-radius: 8px;
`;

const CadastroCliente = () => {
  return (
    <StyledContainer>
      <Typography variant="h4" color="#003366">Cadastro de Cliente</Typography>
      <form>
        <TextField label="Nome" fullWidth margin="normal" />
        <TextField label="CPF" fullWidth margin="normal" />
        <TextField label="Telefone" fullWidth margin="normal" />
        <TextField label="E-mail" fullWidth margin="normal" />
        <TextField label="Endereço" fullWidth margin="normal" />
        <TextField label="Veículo (Placa)" fullWidth margin="normal" />
        <TextField label="Modelo do Veículo" fullWidth margin="normal" />
        <TextField label="Ano do Veículo" fullWidth margin="normal" />
        <Button variant="contained" color="primary" type="submit">Cadastrar</Button>
      </form>
    </StyledContainer>
  );
};

export default CadastroCliente;
